.store {
  &__list-box {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
    padding: 0;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;

    justify-content: center;
    gap: 16px;
    width: 100%;
    margin-bottom: 32px;
    @media screen and (min-width: 768px) {
      gap: 32px;
      margin-bottom: 60px;
    }
  }
  &__item {
    display: flex;
    flex-basis: calc((100% - 32px) / 2);
  }

  &__gallery {
    display: flex;
    flex-direction: column;
    width: 100%;
    display: block;
  }
  &__photo {
    display: block;
    width: 100%;
    height: auto;
    margin-bottom: 12px;
    @media screen and (min-widht: 768px) {
      margin-bottom: 16px;
    }
  }
  &__name {
    font-family: $font-primary;
    font-weight: 500;
    font-size: 16px;
    line-height: 1.25;
    text-align: left;
    color: $text-color;
    margin-top: 12px;
    width: 100%;
    @media screen and (min-width: 768px) {
      font-size: 32px;
      line-height: 1.2;
      margin-top: 16px;
    }
    @media screen and (min-width: 1280) {
      font-size: 36px;
      margin-top: 18px;
    }
    &::after {
      @media screen and (min-width: 768px) {
        content: '';
        width: 100%;
        height: 1px;
        background-color: $text-color;
        display: inline-block;
      }
    }
  }
  &__price {
    display: block;
    font-family: $font-primary;
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 1.25;
    color: $text-color;
    opacity: 0.6;
    margin-top: 6px;
    @media screen and (min-width: 768px) {
      font-size: 24px;
      line-height: 1.2;
      margin-top: 8px;
    }
    @media screen and (min-width: 1280) {
      font-size: 28px;
      margin-top: 4px;
    }
    &--padding {
      @media screen and (min-width: 768px) and (max-width: 1279px) {
        padding-bottom: 8px;
      }

      @media screen and (min-width: 1280px) {
        padding-bottom: 32px;
      }
    }
  }
  .button {
    display: block;
    font-family: 'Montserrat';
    color: $primary-white-color;
    font-weight: 500;
    line-height: 24px;
    text-decoration: none;
    text-align: center;
    cursor: pointer;
    background-color: $primary-orange-color;
    border: none;
    border-radius: 38px;
    &:focus {
      background-color: $primary-orange-color;
    }
    @media screen and (min-width: 768px) {
      font-size: 28px;
      padding: 20px 0;
      width: 280px;
    }
    &--mobile-m {
      padding: 10px 35px;
      font-size: 20px;
    }
    &--desktop-tablet {
      font-size: 28px;
      padding: 20px 0;
      width: 280px;
    }
  }
}
